













































import { Component, Vue } from 'vue-property-decorator';
import Jsbarcode from 'jsbarcode';
import Swal from 'sweetalert2';
import { Action, State } from 'vuex-class';
import MainFooter from '../../components/MainFooter.vue';

@Component({
  components: {
    MainFooter,
  },
})

export default class MainPage extends Vue {
  @State('name') stateName !: string;
  @Action('changeRegisterModeAct') changeRegisterModeAct!: (flag: boolean) => void

  editProfile() {
    this.$router.push({ path: '/member/edit_profile', query: { process: 'member' } });
  }

  editSubscribe() {
    this.$router.push({ path: '/member/edit_subscribe', query: { process: 'member' } });
  }

  showRecord() {
    this.$router.push('/member/test_record');
  }

  interestExam() {
    this.$router.push({ path: '/member/interest_exam', query: { process: 'member' } });
  }

  ticketCenter() {
    this.$router.push('/ticket/main');
  }

  mounted() {
    Jsbarcode('#barcode', '12345678', {
      width: 2,
      height: 50,
      font: 'Noto Sans CJK TC',
      fontSize: 16,
    });
  }

  logout() {
    Swal.fire({
      width: 312,
      showCloseButton: false,
      showCancelButton: true,
      background: '#FFFFFF',
      title: '確定登出',
      customClass: {
        container: 'modal-container',
        popup: 'modal-popup',
        title: 'modal-title',
        content: 'modal-content',
        confirmButton: 'modal-confirm-button-130',
        cancelButton: 'modal-cancel-button-130',
      },
      confirmButtonText: '確定',
      cancelButtonText: '取消',
      reverseButtons: true,
    }).then((res) => {
      if (res.isConfirmed) {
        this.changeRegisterModeAct(false);
        this.$router.push('/');
      }
    });
  }
}
